import React, { useState } from "react";
import ReactPlayer from "react-player";

import { makeStyles, Theme } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";

import UnfoldMoreOutlinedIcon from '@material-ui/icons/UnfoldMoreOutlined';

import VideoPlayer from './VideoPlayer';

interface InstaPostContainerProps {
    list: any
}

const useStyles = makeStyles((theme: Theme) => ({

    root: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        borderRadius: "4px",
        backgroundColor: "#fff",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        margin: "20px 10px 40px 10px",
        height: 'max-content',
        padding: '20px 0',
        position: 'relative',
    },

    pulldown: {
        position: 'absolute',
        bottom: '-26px',
        textAlign: 'center',
        width: '100%',
    },

    pullDownIcon: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        color: '#999',
        fontSize: '40px',
        cursor: 'pointer',
        border: '1px solid transparent',
        borderRadius: '50%',
        background: '#f6f6f6',

        '&:hover': {
            color: '#6c757d',
        }
    },

    videoThumbnail: {
        padding: '10px'
    },

    img: {
        width: '100%',
        borderRadius: '4px',
        cursor: 'pointer',
        // '&:hover': {
        //      width: '100%',
        // }
    },

    gridContainer: {
        padding: '10px',
        [theme.breakpoints.only('xs')]: {
            padding: '0'
        }
    },

    playerWrapper: {
        position: 'relative',
        // paddingTop: '56.25% '/* Player ratio: 100 / (1280 / 720) */
        paddingTop: '45%',
        left: '50%',
        top: '50%',
        transform: 'translate(-50%,-50%)',
    },

    reactPlayer: {
        position: 'absolute',
        top: '0',
        left: '0',
        zIndex: 10,
    },


}));


const InstaPostContainer: React.FC<InstaPostContainerProps> = ({ list }) => {

    const classes = useStyles();
    const [expanded, setExpanded] = useState(false);
    const [playerOpen, setPlayerOpen] = useState(false);
    const [selectedVideo, setVideo] = useState();
    const items = [];

    const init = 4;

    const handleClick = () => {
        setExpanded(!expanded);
    }

    const showHidePlayer = () => {

        setPlayerOpen(!playerOpen);
    }

    const setVideoPlayer = (item: any) => {
        setVideo(item);
        setPlayerOpen(true);
    }

    for (let i = 0; i < (expanded ? list.length : init); i++) {
        let item = list[i];
        items.push(
            <Grid className={classes.videoThumbnail} xs={12} sm={6} md={4} lg={3} >
                <img className={classes.img} src={item.thumbnailUrl}
                    onClick={() => setVideoPlayer(item)}
                />
            </Grid>)
    }

    return (
        <div className={classes.root}>

            <Grid container className={classes.gridContainer}>
                {items}
            </Grid>
            <div className={classes.pulldown}>
                <UnfoldMoreOutlinedIcon className={classes.pullDownIcon} onClick={handleClick} />
            </div>
            {selectedVideo ?
                <VideoPlayer open={playerOpen} handleClose={showHidePlayer} info={selectedVideo} /> : null
            }

        </div>
    );
}

export default InstaPostContainer;