import React, { useState } from "react";
import InstagramEmbed from "react-instagram-embed";

import { makeStyles, Theme } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";

import UnfoldMoreOutlinedIcon from '@material-ui/icons/UnfoldMoreOutlined';

interface InstaPostContainerProps {
    list: any
}

const useStyles = makeStyles((theme: Theme) => ({

    root: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        borderRadius: "4px",
        backgroundColor: "#fff",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        margin: "20px 10px 40px 10px",
        height: 'max-content',
        padding: '20px 0',
        position: 'relative',
    },

    instaPost: {
        padding: '10px 0'
    },

    pulldown: {
        position: 'absolute',
        bottom: '-26px',
        textAlign: 'center',
        width: '100%',
    },

    pullDownIcon: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        color: '#999',
        fontSize: '40px',
        cursor: 'pointer',
        border: '1px solid transparent',
        borderRadius: '50%',
        background: '#f6f6f6',

        '&:hover': {
            color: '#6c757d',
        }
    }

}));


const InstaPostContainer: React.FC<InstaPostContainerProps> = ({ list }) => {

    const classes = useStyles();
    const [expanded, setExpanded] = useState(false);
    const items = [];

    const init = 3;

    const handleClick = () => {
        setExpanded(!expanded);
    }

    for (let i = 0; i < (expanded ? list.length : init); i++) {
        let item = list[i];
        items.push(
            <Grid className={classes.instaPost} xs={12} md={6} lg={4}>
                <InstagramEmbed
                    url={item.url}
                    maxWidth={320}
                    hideCaption={true}
                    containerTagName='div'
                    injectScript={false}
                    protocol=''
                    onLoading={() => { }}
                    onSuccess={() => { }}
                    onAfterRender={() => { }}
                    onFailure={() => { }}
                />
            </Grid>)
    }

    return (
        <div className={classes.root}>
            <Grid container>

                {items}
            </Grid>
            <div className={classes.pulldown}>
                <UnfoldMoreOutlinedIcon className={classes.pullDownIcon} onClick={handleClick} />
            </div>
        </div>
    );
}

export default InstaPostContainer;